<template lang="pug">
  section#Uno
    //-
      01. Front-end Development
    SectionTitle(color="#F2E059" seo="01. I am a Front-end Developer")
      span(slot="number") 1
      span(slot="text1") Front-end
      span(slot="text2") DEVELOPMENT

    blockquote.slogan(v-in-viewport.once="true") I am never afraid to discover new technologies
    blockquote(v-in-viewport.once="true") I like to hack on my way while I learn how to be a better developer. With a passion for all things visual and technical,
      |  my mission is to code high quality responsive websites focusing on exceptional user experiences.

    Techs
</template>


<script>
  import Techs from '@/components/Techs'

  export default {
    components: {
      Techs
    }
  }
</script>


<style lang="sass" scoped>
  #Uno

    blockquote
      color: #efefef
      text-align: left
      line-height: 1.3
      font-weight: 200
      font-size: calc(1.25rem + (3 - 2) * (100vw - 32rem)/ (76.8 - 32))
      margin-top: calc(1.5rem + (6 - 4) * (100vw - 32rem)/ (76.8 - 32))
      opacity: 0
      strong
        font-weight: 600
      &.in-viewport
        animation: .550s fadeIn 1250ms both
      +breakpoint(md)
        font-size: calc(1.5rem + (3 - 2) * (100vw - 32rem)/ (76.8 - 32))
      //
      &.slogan
        color: #fff
        margin-bottom: -0.5em
        font-weight: 500
        &.in-viewport
          animation: 1.25s fadeInUp2 550ms both
</style>



