<template lang="pug">
  ul.social-links
    li.social-links__icon(v-for="icon in icons" v-in-viewport="true")
      a(:href="icon.url" target="_blank" rel="noopener" aria-label="icon.name")
        svg.svgicon
          use(xmlns:xlink="http://www.w3.org/1999/xlink" :xlink:href="'#icon-' + icon.name")
</template>


<script>
  export default {
    name: 'SocialNetworks',

    data: () => ({
      icons: [
        { name: 'github', url: 'https://github.com/whoisjorge' },
        { name: 'twitter', url: 'https://twitter.com/whois_jorge' },
        { name: 'codepen', url: 'https://www.codepen.io/whoisjorge' },
        { name: 'linkedin', url: '#' },
        { name: 'mail', url: 'mailto:email@whoisjorge.me' }
      ]
    })
  }
</script>


<style lang="sass" scoped>
  .social-links
    display: flex
    justify-content: center
    list-style: none
    padding-left: 1.25rem
    +breakpoint(md)
      padding-left: 0
    &__icon
      color: $orange
      font-size: .8rem
      display: inline-block
      margin: 12px 16px
      opacity: 0
      +breakpoint(md)
        margin: 12px 24px
      &:first-child
        margin-left: 2.75rem
      // Animation
      @for $i from 1 through 5
        &:nth-of-type(#{$i})
          &.in-viewport
            animation: (0.5s + ($i - 1) * 0.1s) fadeInUp (10ms + ($i - 1) * 0.1s) both
</style>



