<template lang="pug">
  section#Dos
    //-
      02. Design & User Experience
    SectionTitle(color="#F2E059" seo="02. Design & UI/UX")
      span(slot="number") 2
      span(slot="text1") Design
      span(slot="text2") USER EXPERIENCE

    blockquote.slogan(v-in-viewport.once="true") I bridge the gap between design and code
    
    Techdesign

    blockquote(v-in-viewport.once="true") My speciality is build fresh components following the look and feel of the original designs, 
      |  striving to make engaging, beautiful, and thoughtful web applications.


    //- Techtransforma
</template>

<script>
  import Techdesign from '@/components/Techdesign'
  import Techtransforma from '@/components/Techtransforma'

  export default {
    components: {
      Techdesign,
      Techtransforma
    }
  }
</script>

<style lang="sass" scoped>
  #Dos

  h2
    font-weight: 500
    font-size: 2.0375rem
    letter-spacing: 0.05em
    opacity: 0
    &.in-viewport
      opacity: 1
      animation: .650s fadeInUp2 1ms both
    &:after
      display: block
      margin-top: 2px
      content: ''
      height: 2px
      width: 0
      background-color: $color-contact
    &.in-viewport:after
      transition: width ease .750s
      width: 50px


  blockquote
    color: #efefef
    text-align: left
    line-height: 1.3
    font-weight: 200
    font-size: calc(1.25rem + (3 - 2) * (100vw - 32rem)/ (76.8 - 32))
    margin: calc(4rem + (6 - 4) * (100vw - 32rem)/ (76.8 - 32)) 0
    margin-top: -0.5em
    opacity: 0
    &.in-viewport
      animation: .550s fadeIn 1250ms both
    +breakpoint(md)
      font-size: calc(1.5rem + (3 - 2) * (100vw - 32rem)/ (76.8 - 32))
      // margin-left: 15%

    &.slogan
      color: #fff
      font-weight: bold
      margin-top: calc(2rem + (6 - 4) * (100vw - 32rem)/ (76.8 - 32))
      &.in-viewport
        animation: 1.25s fadeInUp2 550ms both
</style>






