      span(slot="text2") LATEST WORKS

    Projects

    //- blockquote.slogan(v-in-viewport.once="true") Actually updating this... check back later 👋

</template>



<script>
  import Projects from '@/components/Projects'

  export default {
    components: { Projects }
  }
</script>



<style lang="sass" scoped>
#Tres
  min-height: 100vh

  blockquote
    color: #efefef
    text-align: left
    line-height: 1.3
    font-weight: 200
    font-size: calc(1.25rem + (3 - 2) * (100vw - 32rem)/ (76.8 - 32))
    margin: calc(4rem + (6 - 4) * (100vw - 32rem)/ (76.8 - 32)) 0
    opacity: 0
    &.in-viewport
      animation: .550s fadeIn 1250ms both
    +breakpoint(md)
      font-size: calc(1.5rem + (3 - 2) * (100vw - 32rem)/ (76.8 - 32))
      // margin-left: 15%

    &.slogan
      color: #fff
      font-weight: bold
      &.in-viewport
        animation: 1.25s fadeInUp2 550ms both

    a 
      border-bottom: 1px solid #9aefdd
      &:hover
        color: #9aefdd
        border-bottom-color: #efefef



    .svgicon 
      width: 24px
      height: 24px
</style>



